import { message, Spin } from 'antd';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import ParseTime from '../../Functions/ParseTime';
import { CalendarOutlined } from '@ant-design/icons';
import projectIcon from '../../assets/Icons/project.png';
const ViewProject = ({ match }) => {
  const [data, setdata] = useState();
  const [userdata, setuserdata] = useState();
  const [spin, setSpin] = useState();

  const {
    params: { projectId },
  } = match;

  useEffect(() => {
    setSpin(true);
    axios
      .get(
        `https://ginnovation-server.herokuapp.com/api/viewproject/${projectId}`
      )
      .then((resp) => {
        setSpin(false);
        setdata(resp.data);
        axios
          .get(
            `https://ginnovation-server.herokuapp.com/api/profile/users/${resp.data.creatorsID}`
          )
          .then((resp) => {
            setuserdata(resp.data);
          })
          .catch((err) => {
            message.error('error fetching the user data');
          });
      })
      .catch((err) => {
        setSpin(false);
        console.log(err);
        message.error('error');
      });
  }, [projectId]);

  const link = data && data.projectLink;
  const embedLink =
    data && [link.slice(0, 13), 'be.com/embed/', link.slice(17)].join('');
  const source = data && link.substring(0, 16);
  console.log(source);
  return (
    <div className='container blog-details project-details'>
      {spin && (
        <div className='modal-container'>
          <Spin size='large' />
        </div>
      )}
      {data && (
        <>
          <div className='head'>
            <h3>
              <img src={projectIcon} alt='ginnovation project' /> Ginnovation
              Projects
            </h3>
          </div>
          <header>
            <div
              className='profile-img'
              style={{
                backgroundImage: userdata && `url(${userdata.profilePicture})`,
              }}
            ></div>
            <h4>{userdata && userdata.fullname}</h4>
            {userdata && (
              <p>
                {userdata.education[0].semester +
                  ' Semester' +
                  ', ' +
                  userdata.education[0].branch}
              </p>
            )}
            <p className='flex-center'>
              <CalendarOutlined className='icon' />
              Created on {ParseTime(data.createdAt)}
            </p>
          </header>
          <hr />
          <div className='blog-content container'>
            <div className='heading'>
              <h1>{data.projectTitle}</h1>
              <h5>{data.projectSubTitle}</h5>
            </div>
            <p className='content'>{data.desc}</p>
          </div>
          <div className='flex-center container'>
            {source === 'https://youtu.be' ? (
              <iframe
                width='100%'
                height='500'
                src={embedLink}
                frameBorder='0'
                allow='accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture'
              ></iframe>
            ) : (
              <img
                src='https://www.lemonlight.com/app/uploads/2019/01/Twitter.png'
                alt='noimg'
              />
            )}
          </div>
        </>
      )}
    </div>
  );
};

export default ViewProject;
