import React from 'react';
import Avatar from 'antd/lib/avatar/avatar';
import { Link } from 'react-router-dom';
import { DeleteOutlined } from '@ant-design/icons';
import { Col, Row } from 'antd';
import axios from 'axios';
//-----------------------------------------------------------------
const ProjectCard = ({ value, editProject }) => {
  const link = value.projectLink !== null ? value.projectLink : 'aaaaa';
  const embedLink = [link.slice(0, 13), 'be.com/embed/', link.slice(17)].join(
    ''
  );
  const description = value.desc.slice(0, 100) + '...';
  const source = link.substring(0, 16);
  const projectRouteLink = `/project/${value._id}`;
  const projectTitle = value.projectTitle.slice(0, 30) + '...';

  return (
    <div>
      <div className='video-cards'>
        <div className='card-info'>
          <div className='youtube-video-container'>
            {source === 'https://youtu.be' ? (
              <iframe
                width='560'
                height='315'
                src={embedLink}
                frameBorder='0'
                allow='accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture'
              ></iframe>
            ) : (
              <img
                src='https://www.lemonlight.com/app/uploads/2019/01/Twitter.png'
                alt='noimg'
              />
            )}
          </div>
          <div className='name'>
            <Avatar
              className='avatar'
              style={{ color: '#f56a00', backgroundColor: '#fde3cf' }}
            >
              P
            </Avatar>
            <h6>{value.teamName}</h6>
          </div>
          <h5 className='title'>{projectTitle}</h5>
          <p className='subtitle'>{value.projectSubTitle}</p>
          <p className='desc'>{description}</p>

          <Row className='button-group' gutter={4}>
            <Col xs={21}>
              <Link to={projectRouteLink}>
                <button>View Details</button>
              </Link>
            </Col>
            <Col xs={3}>
              <button
                className='delete'
                onClick={() => {
                  editProject(value);
                }}
              >
                <DeleteOutlined />
              </button>
            </Col>
          </Row>
        </div>
      </div>
    </div>
  );
};
export default ProjectCard;
